<template>
  <b-card
    fluid
    class="py-1 px-2"
  >
    <modalAddParticipante @refreshTabla="refreshTabla" />
    <b-row class="ml-1">
      <h5 class="">
        <b-badge variant="primary">
          <feather-icon
            icon="ApertureIcon"
          />
        </b-badge> <strong>Taller Cultural</strong> : {{ nombre_taller }}
      </h5>
      <h5>
        ( <b-badge
          :variant="(estado_taller == 1) ? 'light-success' : 'light-danger'"
          class="px-2"
        >
          {{ (estado_taller == 1) ? 'ABIERTO' : 'CERRADO' }}
        </b-badge> )
      </h5>
    </b-row>
    <b-row class="ml-1 my-2">
      <h3 class="text-primary">

        <feather-icon
          icon="UsersIcon"
        />
        ESTUDIANTES
      </h3>
    </b-row>
    <b-row>
      <b-col
        cols="4"
      >
        <label>Mostrar</label>
        <b-form-select
          id="per-page-select"
          v-model="perPage"
          :options="pageOptions"
          size="sm"
          class="per-page-selector d-inline-block mx-50"
          style="width:auto"
          @change="actualizar_to_from"
        />
        <label>registros</label>
      </b-col>
      <b-col
        cols="4"
      >
        <b-button
          variant="outline-primary"
          class="px-3"
          size="sm"
          @click="abrirModal"
        >
          <feather-icon icon="PlusCircleIcon" /> Matricular Estudiante
        </b-button>

      </b-col>
      <b-col
        cols="4"
        class="pb-1"
      >
        <b-form-group
          class="mb-0"
        >
          <b-input-group>
            <b-form-input
              id="filter-input"
              v-model="filter"
              type="search"
              placeholder="Buscar..."
              size="sm"
            />

            <b-input-group-append>
              <b-button
                :disabled="!filter"
                variant="success"
                size="sm"
                @click="filter = ''"
              >
                Limpiar
              </b-button>
            </b-input-group-append>
          </b-input-group>
        </b-form-group>
      </b-col>
    </b-row>
    <b-table
      :items="estudiantesTaller"
      :fields="fields"
      :current-page="currentPage"
      :per-page="perPage"
      :filter="filter"
      :filter-included-fields="filterOn"
      :sort-by.sync="sortBy"
      :sort-desc.sync="sortDesc"
      :sort-direction="sortDirection"
      stacked="sm"
      head-variant="light"
      class="position-relative"
      show-empty
      small
      hover
      :empty-text="'No hay registros para mostrar'"
      :empty-filtered-text="'No hay registros que coincidan con su solicitud.'"
      responsive="sm"
      @filtered="onFiltered"
    >
      <template #cell(acciones)="row">
        <b-dropdown
          variant="link"
          no-caret
          :right="$store.state.appConfig.isRTL"
        >

          <template #button-content>
            <feather-icon
              icon="MoreVerticalIcon"
              size="16"
              class="align-middle text-body"
            />
          </template>
          <b-dropdown-item>
            <feather-icon icon="EditIcon" />
            <span class="align-middle ml-50">Evaluación</span>
          </b-dropdown-item>
          <b-dropdown-item
            @click="reporte(row.item)"
          >
            <feather-icon icon="DownloadIcon" />
            <span class="align-middle ml-50">Descargar constancia</span>
          </b-dropdown-item>
          <b-dropdown-item
            @click="eliminarEstudiante(row.item.id)"
          >
            <feather-icon icon="TrashIcon" />
            <span class="align-middle ml-50">Retirar del taller</span>
          </b-dropdown-item>
        </b-dropdown>
      </template>
      <template #cell(estudiante)="row">
        {{ row.item.persona_alumno.apellido_paterno }} {{ row.item.persona_alumno.apellido_materno }} {{ row.item.persona_alumno.nombres }}
      </template>
      <template #cell(numero_documento)="row">
        {{ row.item.persona_alumno.numero_documento }}
      </template>
      <template #cell(pueblo_indigena)="row">
        {{ row.item.persona_alumno.pueblo_indigena.nombre }}
      </template>
    </b-table>
    <b-row
      cols="12"
      class="d-flex justify-content-between mb-1 mb-md-0"
    >
      <b-col cols="6">
        <span class="text-muted">Mostrando registros del {{ from }} al {{ to }} de un total de {{ totalRows }} registros</span>
      </b-col>
      <b-col cols="4">
        <b-pagination
          v-model="currentPage"
          :total-rows="totalRows"
          :per-page="perPage"
          align="fill"
          size="sm"
          class="my-0"
          @input="actualizar_to_from"
        />
      </b-col>

    </b-row>
  </b-card>
</template>

<script>
import {
  BCard, BDropdown, BDropdownItem, BBadge, BFormSelect, BButton, BFormGroup, BFormInput, BRow, BCol, BInputGroup, BPagination, BTable, BInputGroupAppend,
} from 'bootstrap-vue'
import store from '@/store'
import { onUnmounted } from '@vue/composition-api'
import jsPDF from 'jspdf'
import { mapState } from 'vuex'
import estudiantesTallerStoreModule from './estudiantesTallerStoreModule'
import modalAddParticipante from './ModalAddAlumno.vue'

export default {
  components: {
    modalAddParticipante,
    BButton,
    BDropdown,
    BDropdownItem,
    BFormGroup,
    BFormInput,
    BRow,
    BCol,
    BInputGroup,
    BFormSelect,
    BPagination,
    BTable,
    BInputGroupAppend,
    BBadge,
    BCard,
  },

  data() {
    return {
      id: this.$attrs.id,
      tallerGlobal_id: this.$attrs.tallerGlobal_id,
      nombre_taller: '',
      estado_taller: '',
      items: [],
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15, { value: 100, text: 'Ver todo' }],
      sortBy: '',
      sortDesc: false,
      sortDirection: 'asc',
      filter: null,
      filterOn: [],
      from: 0,
      to: 0,
      totalRows: 0,
    }
  },
  computed: {
    ...mapState('estudiantes_taller', ['estudiantesTaller']),
  },
  mounted() {
    store.state.estudiantes_taller.curso_taller_id = this.id
    store.state.estudiantes_taller.tallerGlobal_id = this.tallerGlobal_id
    store.dispatch('estudiantes_taller/fetchEstudiantesTaller').then(() => {
      if (store.state.estudiantes_taller.estudiantesTaller !== undefined) {
        this.totalRows = store.state.estudiantes_taller.estudiantesTaller.length || 0
        this.actualizar_to_from()
      }
    })
    store.dispatch('estudiantes_taller/fetchCursoTaller').then(response => {
      console.log('message: ', response)
      this.nombre_taller = response.data.nombre
      this.estado_taller = response.data.estado
    })
  },
  methods: {
    refreshTabla() {
      store.dispatch('estudiantes_taller/fetchEstudiantesTaller').then(response => {
        this.totalRows = response.data.data.length
        this.actualizar_to_from()
      })
    },
    actualizar_to_from() {
      const { totalRows } = this
      if (totalRows === 0) {
        this.from = 0
        this.to = 0
      } else
      if (this.currentPage === 1) {
        this.from = 1
        if (totalRows <= this.perPage) {
          this.to = totalRows
        } else {
          this.to = this.perPage
        }
      } else {
        this.from = ((this.currentPage - 1) * this.perPage) + 1
        if ((this.currentPage * this.perPage) <= totalRows) {
          this.to = this.currentPage * this.perPage
        } else {
          this.to = totalRows
        }
      }
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
      this.actualizar_to_from()
    },
    abrirModal() {
      this.$bvModal.show('modalAddParticipante')
    },
    reporte(item) {
      // eslint-disable-next-line new-cap
      const doc = new jsPDF({
        orientation: 'landscape',
      })
      const logo = new Image()
      logo.src = '@/assets/images/layouts/fondo-unia.jpg'
      doc.addImage(logo, 'JPEG', 100, 10, 160, 180)

      doc.setFontSize(20)
      doc.text('UNIVERSIDAD NACIONAL INTERCULTURAL DE LA AMAZONÍA', 60, 10)

      doc.setFontSize(16)
      doc.text("NON BENA BAI 'NUESTRO CAMINO SEGURO'", 100, 20)

      doc.setFontSize(18)
      doc.text('CERTIFICADO', 130, 40)

      doc.setFontSize(16)
      doc.text('La Dirección de Bienestar Universitario certifica que:', 85, 50)

      doc.setFontSize(18)
      doc.text(item.nombres, 115, 70)

      doc.setFontSize(16)
      doc.text(`Se inscribió al taller de:${this.nombre_taller}`, 98, 80)

      doc.setFontSize(15)
      doc.text('Obteniendo el resultado de: APROBADO', 112, 90)

      doc.setFontSize(12)
      doc.text('Pucallpa', 140, 120)

      doc.setFontSize(12)
      doc.text('05-08-2021', 138, 130)

      doc.save('a4.pdf')
    },
    eliminarEstudiante(id) {
      this.$swal({
        title: '¿Esta seguro de retirar al estudiante del taller? ',
        text: 'Esta acción es irreversible!',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Si, retirar!',
        customClass: {
          confirmButton: 'btn btn-primary',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          store.dispatch('estudiantes_taller/deleteEstudianteTaller', id).then(() => {
            this.refreshTabla()
            this.$swal({
              icon: 'success',
              title: 'Realizado!',
              text: 'Estudiante retirado.',
              customClass: {
                confirmButton: 'btn btn-success',
              },
            })
          })
        }
      })
    },
  },
  setup() {
    const ESTUDIANTES_TALLER_APP_STORE_MODULE_NAME = 'estudiantes_taller'

    // Register module
    if (!store.hasModule(ESTUDIANTES_TALLER_APP_STORE_MODULE_NAME)) store.registerModule(ESTUDIANTES_TALLER_APP_STORE_MODULE_NAME, estudiantesTallerStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(ESTUDIANTES_TALLER_APP_STORE_MODULE_NAME)) store.unregisterModule(ESTUDIANTES_TALLER_APP_STORE_MODULE_NAME)
    })
    const fields = [
      // { key: 'usuario_id', label: 'usuario_id', sortable: true },
      { key: 'numero_documento', label: 'DNI', sortable: true },
      { key: 'estudiante', label: 'Estudiante', sortable: true },
      // { key: 'apellido_materno', label: 'Ape.Materno', sortable: true },
      // { key: 'nombres', label: 'Nombres', sortable: true },
      { key: 'pueblo_indigena', label: 'Etnia', sortable: true },
      { key: 'acciones', label: 'acciones' },
    ]
    return {
      fields,
    }
  },
}
</script>
