import axios from '@axios'

export default {
  namespaced: true,
  state: {
    estudiantesTaller: [],
    curso_taller_id: '',
    tallerGlobal_id: '',
  },
  getters: {},
  mutations: {
    listarEstudiantesMatriculados(state, valor) {
      state.estudiantesTaller = valor.data
    },
  },
  actions: {
    fetchEstudiantesTaller(ctx) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-cultural/taller/${ctx.state.curso_taller_id}/matricula`, { withCredentials: false })
          .then(response => { console.log('DATAAAAAAEEEAAA: ', response); ctx.commit('listarEstudiantesMatriculados', response.data); resolve(response) })
          .catch(error => reject(error))
      })
    },
    addEstudianteTaller(ctx, data) {
      return new Promise((resolve, reject) => {
        axios
          .post(`/servicio-cultural/taller/${ctx.state.curso_taller_id}/matricula`, data)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    deleteEstudianteTaller(ctx, id) {
      return new Promise((resolve, reject) => {
        axios
          .delete(`/servicio-cultural/taller/${ctx.state.curso_taller_id}/matricula/${id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    searchAlumno(ctx, cadenaBuscar) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/buscar-persona2?query=${cadenaBuscar}`, { withCredentials: false })
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    fetchCursoTaller(ctx) {
      return new Promise((resolve, reject) => {
        axios
          .get(`servicio-cultural/taller-global/${ctx.state.tallerGlobal_id}/taller/${ctx.state.curso_taller_id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
  },

}
